.modal {
  display: none;
  align-items: center;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  background: rgba(0, 0, 0, .9);
  overflow: hidden;
  z-index: 15;
}

.modal--open {
  display: flex;
}

.modal section {
  position: relative;
  width: 70%;
  height: fit-content;
  margin: 0 auto;
  padding: 8px 8px 4px;
  background: #fff;
  border-radius: 8px;
  transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
}

.modal section button {
  position: absolute;
  right: 0px;
  top: -37px;
  width: 100px;
  height: 20px;
  padding: 12px;
  border: unset;
  border-radius: 8px 8px 0 0;
  box-sizing: content-box;
  background: #fff;
  cursor: pointer;
}

@media only screen and (max-width: 720px) {
  .modal section {
    width: 90%;
  }

  .modal section button {
    left: 50%;
    transform: translateX(-50%);
  }

  iframe {
    height: 180px;
  }
}