@import url(https://fonts.googleapis.com/css2?family=Black+Han+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700&display=swap);
.header {
  position: fixed;
  top: 0;
  width: 100%;
  min-width: 320px;
  height: 80px;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 10px;
  z-index: 10;
}

.header__logo {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  max-width: 1920px;
  height: 80px;
  line-height: 80px;
  margin: 0 5%;
  font-family: 'Black Han Sans', sans-serif !important;
  font-size: 1.8rem;
}

.header__logo::after {
  content: "";
  float: right;
  width: 15px;
  height: 15px;
  margin: 15px 10px 10px;
  border-radius: 50%;
  background: #FF7B54;
}
.footer {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100px;
  text-align: center;
  z-index: 10;
}

.information {
  max-width: 1920px;
  height: 100px;
  line-height: 120px;
  margin: 0 5%;
}

.information a {
  text-decoration: none;
}

.main-movie {
  position: relative;
  width: 100vw;
  height: 700px;
  background-size: cover;
  background-repeat: no-repeat;
}

.main-movie__gradient {
  width: 100%;
  height: 100px;
  position: absolute;
  bottom: 0;
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.5));
}

.main-movie__poster {
  position: absolute;
  bottom: 50px;
  left: 5%;
  max-width: 250px;
  border-radius: 5px;
  box-shadow: -2px 6px 19px 0px #7f818e;
  cursor: pointer;
}

.main-movie__title {
  position: absolute;
  top: 180px;
  right: 4%;
  color: #fff;
  font-size: 3rem;
}

.main-movie__summary {
  position: absolute;
  width: 50%;
  bottom: 50px;
  right: 4%;
  color: #fff;
  font-size: 1rem;
  text-align: right;
  white-space: normal;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;

}

@media only screen and (max-width: 425px) {
  .main-movie {
    height: 90vh;
  }

  .main-movie__poster {
    display: none;
  }

  .main-movie__title {
    position: absolute;
    top: 40%;
    left: 4%;
    color: #fff;
  }

  .main-movie__summary {
    width: 70%;
    bottom: 5%;
    left: 4%;
    text-align: left;
  }

}
.card-movie {
  position: relative;
  display: flex;
  flex-basis: 49%;
  min-width: 625px;
  max-width: 625px;
  height: 280px;
  padding: 20px;
  background: #fff;
  border-radius: 8px;
  border: 1px solid rgba(70, 77, 82, 0.082);
  box-sizing: border-box;
  box-shadow: rgba(0, 0, 0, 0.024) -1px 1px 1px;
}

.card-movie__poster {
  position: absolute;
  top: -20px;
  left: 20px;
  width: 180px;
  border-radius: 5px;
  box-shadow: -2px 6px 19px 0px #7f818e;
  cursor: pointer;
}

.card-movie__trailer {
  position: absolute;
  top: -20px;
  left: 20px;
  width: 180px;
  height: 270px;
  border-radius: 5px;
  box-shadow: -2px 6px 19px 0px #7f818e;
  z-index: 5;
}

.card-movie__trailer button {
  visibility: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 35px;
  padding: 8px 12px;
  border: 1.8px solid #fff;
  border-radius: 20px;
  background: unset;
  text-align: center;
  text-transform: uppercase;
  line-height: 2px;
  font-size: 0.8rem;
  color: #fff;
  cursor: pointer;
}

.card-movie__trailer:hover {
  top: -30px;
  background: rgba(0, 0, 0, 0.7);
  transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
}

.card-movie__trailer:hover>button {
  visibility: visible;
  top: 45%;
  transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
}

.card-movie__trailer:hover+.card-movie__poster {
  top: -30px;
  transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
}

.card-movie__heart {
  width: 28px;
  height: 28x;
  position: absolute;
  top: 0;
  left: 375px;
  cursor: pointer;
}

.card-movie__data {
  position: absolute;
  left: 200px;
  width: 50%;
  height: 240px;
  padding: 0 30px;
  overflow: hidden;
}

.card-movie__title {
  width: inherit;
  margin: 0 0 12px 2px;
  font-size: 1.3rem
}

.card-movie__genres {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 0.8rem;
}

.card-movie__genre {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-right: 4px;
  margin-bottom: 4px;
  padding: 4px 8px;
  border: 1px solid rgba(70, 77, 82, 0.082);
  box-sizing: border-box;
  box-shadow: rgba(0, 0, 0, 0.024) -1px 1px 1px;
  border-radius: 20px;
  cursor: pointer;
}

.card-movie__summary {
  position: absolute;
  bottom: 0;
  margin-top: 60px;
  white-space: normal;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
  font-size: 0.9rem;
  line-height: 1.7;
}

@media only screen and (max-width: 425px) {
  .card-movie {
    width: 100%;
    min-width: 300px;
    max-width: unset;
    flex-basis: unset;
    margin: 0 4%;
  }

  .card-movie__trailer:hover,
  .card-movie__trailer:hover+.card-movie__poster {
    top: 0;
  }

  .card-movie__trailer,
  .card-movie__poster {
    top: -1px;
    left: 0;
    height: 281px;
    border-radius: 5px 0 0 5px;
    box-shadow: unset;
  }

  .card-movie__data {
    width: 40%;
    left: 180px;
    padding: 0 10px;
  }

  .card-movie__heart,
  .card-movie__summary {
    display: none;
  }

  .card-movie__genres {
    display: unset;
    position: absolute;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    bottom: 0;
  }
}

@-webkit-keyframes loading {
  to {
    background-position-x: -20%;
  }
}

@keyframes loading {
  to {
    background-position-x: -20%;
  }
}

