  }

  .main-skeleton__poster {
    display: none;
  }

  .main-skeleton__title {
    position: absolute;
    width: 80%;
    top: 45%;
    left: 4%;
  }

  .main-skeleton__summary {
    width: 70%;
    height: 100px;
    left: 4%;
    bottom: 5%;
  }

  .list-skeleton {
    grid-gap: 30px 20px;
    gap: 30px 20px;
    margin: 10% 0 0;
  }

  .card-skeleton {
    width: 100%;
    min-width: 300px;
    max-width: unset;
    flex-basis: unset;
    margin: 0 4%;
  }

  .card-skeleton__poster {
    top: -1px;
    left: -1px;
    height: 281px;
    border-radius: 5px 0 0 5px;
    box-shadow: unset;
  }

  .card-skeleton__data {
    width: 40%;
