    padding: 0 10px;
  }

  .card-skeleton__title {
    width: 80%;
  }

  .card-skeleton__genres {
    position: absolute;
    width: 70%;
    height: 100px;
    bottom: 0;
    margin: 0;
  }

  .card-skeleton__heart,
  .card-skeleton__summary {
    display: none;
  }

}

.modal {
  display: none;
  align-items: center;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  background: rgba(0, 0, 0, .9);
  overflow: hidden;
  z-index: 15;
}

.modal--open {
  display: flex;
}

.modal section {
  position: relative;
  width: 70%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 0 auto;
  padding: 8px 8px 4px;
  background: #fff;
  border-radius: 8px;
  transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
}

.modal section button {
  position: absolute;
  right: 0px;
  top: -37px;
  width: 100px;
  height: 20px;
  padding: 12px;
  border: unset;
  border-radius: 8px 8px 0 0;
  box-sizing: content-box;
  background: #fff;
  cursor: pointer;
}

@media only screen and (max-width: 720px) {
  .modal section {
    width: 90%;
  }

  .modal section button {
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }

  iframe {
    height: 180px;
  }
}
/* --- RESET --- */
* {
  font-family: 'Noto Sans KR', sans-serif !important;
}

html {
  font-size: 16px;
  overflow-x: hidden;
}

body {
  position: relative;
  width: 100vw;
  margin: 0;
  font-family: 'Noto Sans KR', sans-serif;
  background: #eaebec;
  overflow-x: hidden;
}

button:focus {
  border: none;
  outline: none;
}

a {
  color: unset;
  background-color: transparent;
}

a:active,
a:hover {
  outline: 0;
}

/* --- APP --- */
.container {
  position: relative;
  padding-bottom: 130px;
  margin-top: 80px;
}

.list-movie {
  display: flex;
  grid-gap: 50px 20px;
  gap: 50px 20px;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 1920px;
  margin: 5% auto 0;
}

/* --- Media --- */
@media only screen and (max-width: 375px) {
  html {
    font-size: 12px !important;
  }
}

@media only screen and (min-width: 376px) and (max-width: 425px) {
  html {
    font-size: 14px !important;
  }

  .list-movie {
    grid-gap: 30px 20px;
    gap: 30px 20px;
    margin: 10% 0 0;
  }
}
