.main-movie {
  position: relative;
  width: 100vw;
  height: 700px;
  background-size: cover;
  background-repeat: no-repeat;
}

.main-movie__gradient {
  width: 100%;
  height: 100px;
  position: absolute;
  bottom: 0;
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.5));
}

.main-movie__poster {
  position: absolute;
  bottom: 50px;
  left: 5%;
  max-width: 250px;
  border-radius: 5px;
  box-shadow: -2px 6px 19px 0px #7f818e;
  cursor: pointer;
}

.main-movie__title {
  position: absolute;
  top: 180px;
  right: 4%;
  color: #fff;
  font-size: 3rem;
}

.main-movie__summary {
  position: absolute;
  width: 50%;
  bottom: 50px;
  right: 4%;
  color: #fff;
  font-size: 1rem;
  text-align: right;
  white-space: normal;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;

}

@media only screen and (max-width: 425px) {
  .main-movie {
    height: 90vh;
  }

  .main-movie__poster {
    display: none;
  }

  .main-movie__title {
    position: absolute;
    top: 40%;
    left: 4%;
    color: #fff;
  }

  .main-movie__summary {
    width: 70%;
    bottom: 5%;
    left: 4%;
    text-align: left;
  }

}